@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
    font-family: "Poppins", sans-serif;
}

.sliderContainer img {
    animation: fadeImg 1s ease-out;
}

@keyframes fadeImg {
    from {
        opacity: 0.8;
    }

    to {
        opacity: 1;
    }
}

.discriptionTruncate-2 {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.no-scrollbar::-webkit-scrollbar {
    display: none;
}
.select__control {
    border: 2px solid #004b95 !important;
    height: 52px !important;
    border-radius: 0.5rem !important;
}
.css-1p3m7a8-multiValue {
    background-color: #004b95 !important;
}
.css-wsp0cs-MultiValueGeneric {
    color: white !important;
}

.filepond--item {
    width: calc(20% - 0.5em);
    max-height: 100px;
    min-height: 100px;
}

.filepond--panel .filepond--item-panel {
    display: none;
}

input[type="date"]::-webkit-datetime-edit-text,
input[type="date"]::-webkit-datetime-edit-month-field,
input[type="date"]::-webkit-datetime-edit-day-field,
input[type="date"]::-webkit-datetime-edit-year-field {
    color: #004b95;
}

input[type="file"]::-webkit-file-upload-button {
    background: #004b95;
    color: white;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

input[type="file"] {
    color: #004b95;
}

select {
    width: 100%;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    padding: 10px;
    margin: 20px 0;
    color: #004b95;
    background: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='100' height='100' fill='%23004b96'><polygon points='0,0 100,0 50,50'/></svg>") no-repeat;
    background-size: 16px;
    background-position: calc(100% - 20px) 20px;
    background-repeat: no-repeat;
}
select option {
    color: #004b95;
}
select option:hover {
    background-color: #004b95;
    color: white;
}

.fr-svg {
    filter: invert(11%) sepia(96%) saturate(4643%) hue-rotate(200deg) brightness(70%) contrast(101%);
}
.fr-active {
    box-shadow: 0px 0px 0px 2px #004b95 inset;
}
.fr-box {
    border: 2px solid #004b95;
}
.fr-toolbar {
    border: none !important;
}
.fr-second-toolbar {
    border: none !important;
}
.fr-wrapper {
    border: none !important;
}

::-webkit-calendar-picker-indicator {
    background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="15" viewBox="0 0 24 24"><path fill="%23004b95" d="M20 3h-1V1h-2v2H7V1H5v2H4c-1.1 0-2 .9-2 2v16c0 1.1.9 2 2 2h16c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zm0 18H4V8h16v13z"/></svg>');
}

/* width */
::-webkit-scrollbar {
    width: 10px;
    opacity: 0.5;
}

/* Track */
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgb(185, 185, 185);
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #9ca3af;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #004b95;
}

@tailwind base;
@tailwind components;
@tailwind utilities;
